@import "../../../_vars.scss";

.authentication-background {
    background-color: $light-grey;
    width: 100%;


    box-shadow: 0px 4px 4px 10px hsla(0, 0%, 0%, 0.25);

    display: flex;
    flex-direction: column;
    justify-content: center; // x-axis if row
    align-items: center; // y-axis if row    
    padding-top: 200px;
    padding-bottom: 286px;
    
    .authentication-title{
        display: flex;
        text-align: center;
        width: 500px;
        padding-bottom: 50px;
        font-family: $font-georgia-bold;
        font-size: 36px;

        color: $black;
    }
    
    .authentication-inputs{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        
    }
}