/* PUT HERE SCSS VARIABLES ( These are base variables from Bookeeping project ) */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&family=Open+Sans&display=swap');

@mixin verticalPadding($size: 10px) {
    padding-top: $size;
    padding-bottom: $size;
}

@mixin horizontalPadding($size: 10px) {
    padding-left: $size;
    padding-right: $size;
}

$font-montserrat: 'Montserrat', sans-serif;
$font-opensans: 'Open Sans', sans-serif;

$color-primary: #0353a4;
$color-secondary: #72b5e8;
$color-white: #ffffff;
$color-gray-bg: #94989c;
$color-gray-light: #b6babf;
$color-gray-dark: #7e848b;

$color-title: #202329;
$color-text: #454d55;

$color-success: #15b881;
$color-warning: #f47b33;
$color-danger: #ee2265;

$border-radius: 6px;


$article-border: 1px solid #AFAFAF;


// Colors
$white: hsl(0, 0%, 100%);
$black: hsl(0, 0%, 0%);
$yellow : hsl(50, 100%, 50%);
$light-yellow : hsl(50, 100%, 81%);

$grey : hsla(0, 0%, 64%, 0.98);
$hover-grey : hsla(0, 0%, 82%, 0.98);
$light-grey : hsl(40, 18%, 90%);
$red : hsl(0, 100%, 50%);
$green : hsl(126, 93%, 35%);
$light-green : rgb(125, 255, 138);

$blue : hsl(216, 93%, 35%);
$light-blue : hsl(217, 100%, 88%);
$dark-blue : hsl(216, 92%, 29%);

// Fonts
@import url('http://fonts.cdnfonts.com/css/palatino-linotype'); // Palatino Linotype
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap'); // Roboto
@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital@0;1&display=swap'); // Merriweather
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&family=Roboto:wght@100&display=swap'); // Comfortaa
@font-face {
    font-family: 'Old English Text MT';
    src: local('OldEnglishTextMT2'),
        url('./assets/fonts/OldEnglishTextMT2.ttf') format('truetype'),
}

$font-palatino-linotype : Palatino Linotype;

$font-roboto : Roboto;
$font-roboto-italic : Roboto,
italic;

$font-merriweather : Merriweather;

$font-old-english-text : 'Old English Text MT';

$font-georgia : Georgia;
$font-georgia-bold : Georgia,
bold;

$font-comfortaa: Comfortaa;